<template>
  <div style="font-size: 30px;width: 100%;height: 100%"  @touchstart="show=true" @touchend="show=false" >
    <br>
    <katex :math="current.a"></katex>
    <br>
    <katex v-if="show" :math="current.b"></katex>

    <button @click.prevent="rand" @touchend.stop @touchstart.stop  style="width: 100%;height: 50px;bottom: 0;left: 0;position: fixed">换一个</button>
    <button @click.prevent="next" @touchend.stop @touchstart.stop  style="width: 100%;height: 50px;bottom: 50px;left: 0;position: fixed">下一个</button>
  </div>
</template>
<script>
import Katex from "@/components/Katex";

export default {
  components: {Katex},
  data:()=>{
    return{
      show:false,
      i:0,
      daoshu:[
        '\\left( x^{\\alpha }\\right) \'=\\alpha x^{\\alpha -1}',
        '\\left( a^{x}\\right) \'=a^{x}\\ln a',
        '\\left( e^{x}\\right) \'=e^{x}',
        '\\left( \\log _{a}x\\right) \'=\\dfrac{1}{x\\ln a}',
        '\\left( \\ln x\\right) \'=\\dfrac{1}{x}',
        '\\left( \\sin x\\right) \'=\\cos x',
        '\\left( \\cos x\\right) \'=-\\sin x',
        '\\left( \\tan x\\right) \'=\\sec ^{2}x',
        '\\left( \\cot x\\right) \'=-\\csc ^{2}x',
        '\\left( \\sec x\\right) \'=\\sec x\\tan x',
        '\\left( \\csc x\\right) \'=-\\csc x\\cot x',
        '\\left( \\arcsin x\\right) \'=\\dfrac{1}{\\sqrt{1-x^{2}}}',
        '\\left( \\arccos x\\right) \'=-\\dfrac{1}{\\sqrt{1-x^{2}}}',
        '\\left( \\arctan x\\right) \'=\\dfrac{1}{1+x^{2}}',
        '\\left( \\arccot x\\right) \'=-\\dfrac{1}{1+x^{2}}',
      ]
    }
  },
  computed:{
    current(){
      let x=this.daoshu[this.i].split('=')
      return {
        a:x[0],
        b:x[1]
      }
    }
  },
  methods:{

    rand(){
      this.show=false
      let i
      do {
        i=Math.floor((Math.random()*this.daoshu.length))
      }while (i===this.i)
      this.i=i
    },
    next(){
      this.show=false
      this.i=(this.i+1)%this.daoshu.length
    }
  }
}
</script>
